<template>
  <div class="context">
    <div class="content">
      <h3 class="title">Изменить пароль:</h3>
      <input id="old-password-input" class="swal2-input" type="password" placeholder="Введите текущий пароль">
      <input id="new-password-input" class="swal2-input" type="password" placeholder="Введите новый пароль">
      <input id="confirm-new-password-input" class="swal2-input" type="password" placeholder="Повторите новый пароль">
      <button v-on:click="changePassword" class="btn btn-primary btn-load btn-lg">Изменить пароль</button>
    </div>
  </div>
</template>

<script>
import "sweetalert2/dist/sweetalert2.css";
import Connection from "@/rsocket/connection";
import notification from "@/notify/notification";
import router from "@/views/router/routes";

export default {
  name: "ChangePassword",
  methods: {
    changePassword() {
      let oldPassword = $('#old-password-input').val();
      if (oldPassword.trim().length === 0) {
        notification('warning', 'Введите текущий пароль');
        return;
      }
      let newPassword = $('#new-password-input').val();
      if (!newPassword.match(/^(?=.*\d)(?=.*[A-z])[0-9a-zA-Z]{6,}$/)) {
        notification('warning', 'Минимальная длина пароля 6 символов, а его составляющая должна включать в себя цифры и латинские символы');
        return;
      }
      let confirmPassword = $('#confirm-new-password-input').val();
      if (newPassword !== confirmPassword) {
        notification('warning', 'Пароли не совпадают');
        return;
      }
      Connection.changePassword(oldPassword, newPassword, (res) => {
        localStorage.setItem("token", JSON.parse(res.data.toString()).newToken);
        notification('success', 'Пароль успешно изменен');
        router.push("/login");
      }, (error) => {
        notification('error', error.prettyText);
      });
    }
  },
  data() {
    return {
      user: [],
    }
  }
}
</script>

<style lang="scss" scoped>
.content {
  box-sizing: border-box;
  background: #222427;
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
  color: white;
  text-align: center;
}
</style>